import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";
import { useFeedbackSubjectTeacherList } from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import { useEffect, useState } from "react";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const StudentFeedbackChart = ({ instituteId }) => {
  const { t } = useTranslation();

  const labels = [];
  const [state, setState] = useState({
    labels,
    datasets: [
      {
        label: "Feedback %",
        data: [],
        backgroundColor: "rgba(255, 99, 132, 0.5)",
      },
    ],
  });
  const { feedbackSubjectTeacherList } = useFeedbackSubjectTeacherList({
    data: {
      id: instituteId,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !instituteId,
  });

  useEffect(() => {
    if (feedbackSubjectTeacherList?.list?.length > 0) {
      let labels = [];
      let obj = {
        label: "Feedback %",
        data: [],
        backgroundColor: "rgba(255, 99, 132, 0.5)",
      };
      for (let st of feedbackSubjectTeacherList?.list) {
        if (st?.staff_emp_code) {
          let per = getInPercentage(
            st?.avg_student_give_feedback,
            st?.student_give_feedback_count
          );
          labels.push(st?.staff_emp_code);
          obj.data?.push(per);
        }
      }
      setState({
        labels,
        datasets: [obj],
      });
    }
  }, [feedbackSubjectTeacherList?.list]);

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("feedback_analytics")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <Bar data={state} options={options} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default StudentFeedbackChart;

const options = {
  responsive: true,
  // plugins: {
  //   legend: {
  //     position: "top",
  //   },
  //   title: {
  //     display: true,
  //     text: "Chart.js Bar Chart",
  //   },
  // },
};

const getInPercentage = (arg1, arg2) => {
  if (arg1 && arg2) {
    let rst = (arg1 / arg2) * 100;
    return rst?.toFixed(2);
  }
  return 0;
};
