import { useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { useFeedbackStudentFeedbackList } from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import StudentFeedbackCard from "./StudentFeedbackCard";
import StudentFeedbackCreate from "./StudentFeedbackCreate";
const StudentFeedback = ({ instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);
  const {
    feedbackStudentFeedbackList,
    feedbackStudentFeedbackListLoading,
    feedbackStudentFeedbackListRefetch,
  } = useFeedbackStudentFeedbackList({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (feedbackStudentFeedbackList?.list?.length) {
      let obj = nextPage(feedbackStudentFeedbackList?.list?.length);
      setState(() => obj);
    }
  }, [feedbackStudentFeedbackList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    feedbackStudentFeedbackListRefetch();
  }, [feedbackStudentFeedbackListRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("all_feedback")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label={t("create_feedback")}
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {feedbackStudentFeedbackList?.list?.map((feedback, index) =>
            feedbackStudentFeedbackList?.list?.length === index + 1 ? (
              <div key={feedback?._id} ref={ref}>
                <StudentFeedbackCard
                  feedback={feedback}
                  onRefetch={onRefetch}
                />
              </div>
            ) : (
              <StudentFeedbackCard
                feedback={feedback}
                key={feedback?._id}
                onRefetch={onRefetch}
              />
            )
          )}
          {feedbackStudentFeedbackListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            feedbackStudentFeedbackList?.list?.length === 0 && (
              <EmptyMessage title="No feedback fuond related this search." />
            )
          ) : (
            feedbackStudentFeedbackList?.list?.length === 0 && (
              <EmptyMessage title="No feedback." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <StudentFeedbackCreate
          onClose={onToggle}
          onRefetch={onRefetch}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default StudentFeedback;
