import { imageShowUrl } from "../../../../services/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import style from "../InstituteFeedback.module.css";

const StudentFeedbackAllStaffCard = ({ staff }) => {
  const { t } = useTranslation();
  return (
    <div className={style.sf_card}>
      <div
        className={style.sf_card_contatiner}
        style={{
          justifyContent: "space-between",
          width: "100%",
        }}
      >
        <div className={style.sf_card_contatiner}>
          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>
              {`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}
            </h6>
            <p>
              {t("employee_code")}
              {": "}
              {staff?.staff_emp_code ?? ""}
            </p>
          </div>
        </div>
        <div className={style.sf_card_contatiner_inner}>
          <h6>
            <span
              style={{
                fontWeight: "400",
              }}
            >
              {t("total_feedback")}
            </span>
            {": "}
            {getInPercentage(
              staff?.avg_student_give_feedback,
              staff?.student_give_feedback_count
            )}
            %
          </h6>
          <p>
            {t("given_feedback")}
            {": "}
            {staff?.student_give_feedback_count ?? 0}
          </p>
        </div>
      </div>
    </div>
  );
};

export default StudentFeedbackAllStaffCard;

const getInPercentage = (arg1, arg2) => {
  if (arg1 && arg2) {
    let rst = (arg1 / arg2) * 100;
    return rst?.toFixed(2);
  }
  return 0;
};
